.site-head {
    padding-block: var(--space-xl);
    display: grid;

    @media (--from-tablet) {
        grid-template-areas: "t t t t t t" "i p . . . .";
        grid-template-columns: max-content max-content repeat(4, 1fr);
        gap: var(--space-m) var(--space-l);
    }
}

.site-head .title {
    grid-area: t;
}

.site-head .info {
    grid-area: i;
}

.site-head .person {
    grid-area: p;
}